import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { StaffService } from 'src/app/api/controllers/Staff';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as moment from 'moment';

@Component({
  selector: 'app-staff-inactive-staff',
  templateUrl: './staff-inactive-staff.component.html',
  styleUrls: ['./staff-inactive-staff.component.scss']
})
export class StaffInactiveStaffComponent implements OnInit {

  staffListData:any;

  isStaffDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  staffData:string = "";

  totalFilterItems:any;

  staffTypeData:any;
  staffOfficalCategoryData:any;
  staffPersonalCategoryData:any;
  staffTypeId:any;

  selectedInput:string = "";
  columnField: any = {}

  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  public params = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }
  constructor(
  	private staffService:StaffService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService,
    private router:Router
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getStaffCategory(id){
     
    var data = _.filter( this.staffTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.staffTypeId = data[0].lookupValueId;
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getStaffSubCategory(id) {
    if(this.staffTypeId == 115){

      var data = _.filter( this.staffOfficalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );
      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    else {

      var data = _.filter( this.staffPersonalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );

      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    

  }
  onGlSearchFilter() {

    if(this.staffData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.staffData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }
  @HostListener('window:onEditMainstaff', ['$event.detail'])
  onEditMainstaff(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let staffId = dataRecord.staffId
    this.router.navigateByUrl('/ams/staff/edit-staff/' + staffId);
  }
  
  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Staff Name',
         datafield: 'ticketId',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Email ID',
         datafield: 'ticketTypeId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Category',
         datafield: 'ticketStatusId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Sub Category',
         datafield: 'ticketCategoryId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Phone Number',
         datafield: 'ticketPriorityId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Actions',
         cellsalign: 'center', 
         align: 'center',
         width: 120,
         cellsrenderer: (row: number): string => {
           return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" onClick="editMainstaff('+ row +')"><i class="icon fa fa-pencil" aria-hidden="true"></i></a></div>'
         },
         renderer: columnrenderer
     }];
     

  	this.staffService.getAllStaffsByApartmentId(this.params).subscribe((res:any) => {

    //filter inactive true items
    this.staffListData = res.filter(data => {
      return !data.isActive
    });

    this.gridSourceData = {
      localdata: this.staffListData,
      datatype: "array"
    }
    this.staffListData = new jqx.dataAdapter(this.gridSourceData);
    console.log("staffListData Inactive",this.staffListData)
    this.totalItems = this.staffListData.length;

    if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }

    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isStaffDataLoaded = true;

  });

  let staffParams = {
    LookupTypeId: 25
  }

    //staff type
    this.lookupService.getLookupValueByLookupTypeId(staffParams).subscribe((res:any) => {
          
       this.staffTypeData = res.filter( item => {
          return item.isActive;
       });

    },
    error => {
      
    }
    );

    let staffOfficialParams = {
      LookupTypeId: 26
    }

    //offical category type
    this.lookupService.getLookupValueByLookupTypeId(staffOfficialParams).subscribe((res:any) => {
          
       this.staffOfficalCategoryData = res.filter( item => {
          return item.isActive;
       });

       console.log(this.staffOfficalCategoryData);

    },
    error => {
      
    }
    );

    let staffPersonalParams = {
      LookupTypeId: 27
    }

    //personal category type
    this.lookupService.getLookupValueByLookupTypeId(staffPersonalParams).subscribe((res:any) => {
          
       this.staffPersonalCategoryData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

  }

}

function editMainstaff(row){
  var event = new CustomEvent('onEditMainstaff', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editMainstaff = editMainstaff;
