import { Component, OnInit, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { StaffService } from 'src/app/api/controllers/Staff';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-staff-gates',
  templateUrl: './staff-gates.component.html',
  styleUrls: ['./staff-gates.component.scss']
})
export class StaffGatesComponent implements OnInit {
  
  gate:any;

  isGateLoaded:boolean = true;

  isGateTableLoaded:boolean = false;

  modalService:any;

  gateListData:any;

  isAddGateLoaded:boolean = false;

  isEditGate:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  constructor(
  	private injector: Injector,
  	public dialog: MatDialog,
    private staffService:StaffService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  submitGateForm(form) {

  	this.isGateLoaded = false;

    if(!this.isEditGate) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "gateName": this.gate.gateName,
        "comments": this.gate.comments,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString()

      }

      let params = {
        gate: details
      }

      this.staffService.addGate(params).subscribe((res:any) => {

        if(res.message) {

          this.isGateLoaded = true;
          this.sharedService.setAlertMessage("Gate added Successfully!");

          this.getAllGates();
        }

      }, error => {

      })

    }

  	else {

      let details = {

        "gateId": parseInt(this.gate.gateId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "gateName": this.gate.gateName,
        "comments": this.gate.comments,
        "isActive": this.gate.isActive,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        gate: details
      }

      this.staffService.updateGate(params).subscribe((res:any) => {

        if(res.message) {

          this.isGateLoaded = true;
          this.sharedService.setAlertMessage("Gate updated Successfully!");

          this.getAllGates();
        }

      }, error => {

      })

    }

	

  }

  getAllGates(){
    
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.staffService.getAllGatesByApartmentId(params).subscribe((res:any) => {

  		this.isGateTableLoaded = true;
  		
      this.gateListData = res.filter(item => {
  			return item.isActive;
  		});

      this.totalItems = this.gateListData.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }

      else {
        this.ItemEndIndex = this.totalItems;
      }


  	}, error => {

	});

  }

  showConfirmModal(item) {
    this.modalService.showConfirmModal(item);
  }

  addGate(){

    this.isEditGate = false;
    this.isAddGateLoaded = false;

    setTimeout(() => {

      this.isAddGateLoaded = true;

    }, 10);

    setTimeout(() => {

      var elem = document.querySelector('.add-gate-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  editGate(id){

    this.isEditGate = true;
    this.isGateLoaded = false;

    this.staffService.getGateByGateId(id).subscribe((res:any) => {

      this.isGateLoaded = true;
      this.gate = res[0];

      this.isAddGateLoaded = false;

      setTimeout(() => {

        this.isAddGateLoaded = true;

      }, 10);

      setTimeout(() => {

        var elem = document.querySelector('.add-gate-card');
        var scrollTo = elem.getBoundingClientRect().top - 100;

        window.scroll({
          top: scrollTo,
          behavior: 'smooth'
        });

      }, 100);


    }, error => {

    });


  }

  closeGate(){
    this.isAddGateLoaded = false;
  }

  ngOnInit() {

  	this.gate = {};

  	this.getAllGates();


    // delete gate
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {

      if(item != null){

        this.isGateTableLoaded = false;

        var params = {
          gateId: item.gateId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.staffService.deleteGate(params).subscribe((res:any) => {

          this.gateListData = this.gateListData.filter((type) => type.gateId !== item.gateId);
          this.sharedService.setUnitListDeleteIndex(null);
          this.isGateTableLoaded = true;

        });

        
      }

    });


  }

}
