import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { StaffService } from 'src/app/api/controllers/Staff';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-add-entry',
  templateUrl: './add-entry.component.html',
  styleUrls: ['./add-entry.component.scss']
})
export class AddEntryComponent implements OnInit {
  
  isEditEntry:boolean = false;

  isEntrySubmitted:boolean = true;

  isEntryError:boolean = false;
  isEntrySuccess:boolean = false;
  alertEntryMessage:string = "";


  entry:any;

  staffDataList:any;
  gatesDataList:any;
  shiftsDataList:any;
  shiftsFilteredDataList:any;
  shiftTypeList:any;

  shiftType:string = "";

  selectedStaffCateogryId:any;
  selectedSubStaffCateogryId:any;
  jobTitle:string = "";

  officalStaffsDataList:any;
  personalStaffsDataList:any;

  attendanceId:any;

  entryComments:string = "";

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private staffService:StaffService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService

  ) { }

  isAdmin(){
    return ( this.cookieService.get('userRoleType') == 'Admin' || this.cookieService.get('userRoleType') == 'Staff') ? true : false;
  }

  isShiftSelected (){
  	return this.shiftType != "" ? true : false;
  }

  getShifts(){
  	this.shiftsFilteredDataList = this.shiftsDataList.filter(item => {
  		return item.shiftTypeId == this.shiftType;
  	})
  }

  getStaffJobTitle(){

  	this.staffService.getStaffByStaffId(this.entry.staffId).subscribe((res:any) => {

  		this.selectedStaffCateogryId = res[0].staffCategoryId || null;
  		this.selectedSubStaffCateogryId = res[0].staffSubCategoryId || null;

  		
  		
  		if(this.selectedStaffCateogryId != null) {
  			// offical staff
  			if(this.selectedStaffCateogryId == 115) {
  				
  				let data = this.officalStaffsDataList.filter(item => {
  					if(item.lookupValueId == this.selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})

  				 if(data === undefined || data.length == 0){
  				 	this.entry.jobTitle = "";
  				 }
  				 else {
  				 	this.entry.jobTitle = data[0].lookupValueName;
  				 }

  				

  			}
  			else {
  				let data = this.personalStaffsDataList.filter(item => {
  					if(item.lookupValueId == this.selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})
  				if(data === undefined || data.length == 0){
  				 	this.entry.jobTitle = "";
  				 }
  				 else {
  				 	this.entry.jobTitle = data[0].lookupValueName;
  				 }
  			}
  		}
  		else {
  			this.entry.jobTitle = "";
  		}


  	});

  }

 

  submitEntryForm(form) {

  	this.isEntrySubmitted = false;

    if(!this.isEditEntry) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "staffId": parseInt(this.entry.staffId),
        "inTime": this.entry.inTime,
        "inGateId": parseInt(this.entry.inGateId),
        "outTime": this.entry.outTime,
        "outGateId": parseInt(this.entry.outGateId),
        "shiftId": parseInt(this.entry.shiftId),
        "notes": this.entryComments,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-12-04T05:43:41.299Z",
        "updatedBy": 0,
        "updatedOn": "2019-12-04T05:43:41.299Z"

      }

      let params = {
        attendance: details
      }

      this.staffService.addAttendance(params).subscribe((res:any) => {
       
       if(res.message){
         this.isEntrySubmitted = true;
         this.sharedService.setAlertMessage("Entry added successfully");
        this.router.navigate(['ams/staff/attendance']);
       }
       else {
         this.isEntrySubmitted = true;
         this.isEntryError = true;
         this.alertEntryMessage = res.errorMessage;
       }

      },
      error => {
        this.isEntrySubmitted = true;
      this.isEntryError = true;
      this.alertEntryMessage = "Some error occured";
      }
      );

    }

  	else {

      let details = {
        "attendanceId":parseInt(this.route.params['value'].id),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "staffId": parseInt(this.entry.staffId),
        "inTime": this.entry.inTime,
        "inGateId": parseInt(this.entry.inGateId),
        "outTime": this.entry.outTime,
        "outGateId": parseInt(this.entry.outGateId),
        "shiftId": parseInt(this.entry.shiftId),
        "notes": this.entryComments,
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": "2019-12-04T05:43:41.299Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-12-04T05:43:41.299Z"

      }

      let params = {
        attendance: details
      }

      this.staffService.updateAttendance(params).subscribe((res:any) => {
       
       if(res.message){
         this.isEntrySubmitted = true;
         this.sharedService.setAlertMessage("Entry updated successfully");
        this.router.navigate(['ams/staff/attendance']);
       }
       else {
         this.isEntrySubmitted = true;
         this.isEntryError = true;
         this.alertEntryMessage = res.errorMessage;
       }

      },
      error => {
        this.isEntrySubmitted = true;
        this.isEntryError = true;
        this.alertEntryMessage = "Some error occured";
      }
      );

    }

	

  }

  ngOnInit() {

  	this.entry = {};
  	this.entry.staffId = "";
  	this.entry.inGateId = "";
  	this.entry.outGateId = "";
  	this.entry.shiftId = "";


    if(this.route.params['value'].id != undefined) {
      
      this.isEditEntry = true;

      this.attendanceId = this.route.params['value'].id;
      
      this.staffService.getAttendanceByAttendanceId(this.route.params['value'].id).subscribe((res:any) => {
          
        this.entry = res[0];
        this.getStaffJobTitle();

        this.staffService.getShiftByShiftId(this.entry.shiftId).subscribe((res:any) => {
           
           this.shiftType = res[0].shiftTypeId.toString();
           this.getShifts();

        },
        error => {
          
        });


      },
      error => {
        
      }
      );

    }

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.staffService.getAllStaffsByApartmentId(params).subscribe((res:any) => {
       
 		this.staffDataList = res.filter(item => {
 			return item.isActive;
 		});

 		


    },
    error => {
      
    }
    );

    let gatesParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.staffService.getAllGatesByApartmentId(gatesParams).subscribe((res:any) => {
       
 		this.gatesDataList = res.filter(item => {
 			return item.isActive;
 		});


    },
    error => {
      
    }
    );

    let shiftParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.staffService.getAllShiftsByApartmentId(shiftParams).subscribe((res:any) => {
       
 		this.shiftsDataList = res.filter(item => {
 			return item.isActive;
 		});

 		

    },
    error => {
      
    }
    );


    //get all shift types

    let shiftTypeParams = {
      LookupTypeId: 28
    }

    this.lookupService.getLookupValueByLookupTypeId(shiftTypeParams).subscribe((res:any) => {
       
 		this.shiftTypeList = res.filter(item => {
 			return item.isActive;
 		});


    },
    error => {
      
    }
    );

    let officalListParams = {
      LookupTypeId: 26
    }

    //get all offical staff 
    this.lookupService.getLookupValueByLookupTypeId(officalListParams).subscribe((res:any) => {
       
 		this.officalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});

    },
    error => {
      
    }
    );

    let personalListParams = {
      LookupTypeId: 27
    }

    //get all personal staffs 
    this.lookupService.getLookupValueByLookupTypeId(personalListParams).subscribe((res:any) => {
       
 		this.personalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});


    },
    error => {
      
    }
    );

  }

}
