import { Component, OnInit, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-staff-setup',
  templateUrl: './staff-setup.component.html',
  styleUrls: ['./staff-setup.component.scss']
})
export class StaffSetupComponent implements OnInit {

  categoryOffcialData:string = "";
  categoryOffcialListData:any;

  categoryPersonalData:string = "";
  categoryPersonalListData:any;

  isCategoryDataLoaded:boolean = false;

  staffCategoryData:any;

  modalService:any;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isStaffLoaded:boolean = false;

  staffType:string = "";
  staffTypeId:number;
  staffCategory:string = "";
  staffCategoryId:number;

  staffDeleteTypeId:string = ""

  errorMessage:string = "";
  isError:boolean = false; 


  isEditStaffCategory:boolean = false;


  constructor(
    private injector: Injector,
    public dialog: MatDialog,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  editStaffCategory(category) {

    this.isEditStaffCategory = true;
    this.staffCategory = category.lookupValueName;
    this.staffCategoryId = category.lookupValueId;
    

  }

  submitStaffForm(form) {
    
    this.isError = false;


    //offical category
    if(this.staffType == "115"){

      this.staffTypeId = 26;

      var categoryAvailable = _.some( this.categoryOffcialData, item => {
        return item.lookupValueName === this.staffCategory;
      });

      if(categoryAvailable) {
        this.isError = true;
        this.errorMessage = 'Category already available';
        this.isStaffLoaded = true;
      }
      else {

        this.isStaffLoaded = false;

        if(!this.isEditStaffCategory) {

          let details = {
  
            "lookupTypeId": this.staffTypeId,
            "lookupValueName": this.staffCategory,
            "description": this.staffCategory,
            "isActive": true,
            "insertedBy": parseInt(this.cookieService.get('userId')),
            "insertedOn": "2019-11-16T14:50:08.217Z",
            "updatedBy": 0,
            "updatedOn": "2019-11-16T14:50:08.217Z"

          }

          let params = {
            lookupvalue: details
          }

          this.lookupService.addLookupValue(params).subscribe((res:any) => {


              this.isStaffLoaded = true;
              this.isCategoryDataLoaded = false;

              let params = {
                LookupTypeId: this.staffTypeId
              }

              this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

                 var categoryOffcialListData = res.filter(item => {
                  return item.isActive;
                 });

                 this.categoryOffcialListData = categoryOffcialListData;
                 this.isCategoryDataLoaded = true;


              },
              error => {

              }
              );

        },
        error => {

        }
        );

        }

        else {

          let details = {

            "lookupValueId": this.staffCategoryId,
            "lookupTypeId": this.staffTypeId,
            "lookupValueName": this.staffCategory,
            "description": this.staffCategory,
            "isActive": true,
            "insertedBy": 0,
            "insertedOn": "2019-11-16T14:50:08.217Z",
            "updatedBy": parseInt(this.cookieService.get('userId')),
            "updatedOn": "2019-11-16T14:50:08.217Z"

          }

          let params = {
            lookupvalue: details
          }

          this.lookupService.updateLookupValue(params).subscribe((res:any) => {


              this.isStaffLoaded = true;
              this.isCategoryDataLoaded = false;
              this.isEditStaffCategory = false;

              let params = {
                LookupTypeId: this.staffTypeId
              }

              this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

                 var categoryOffcialListData = res.filter(item => {
                  return item.isActive;
                 });

                 this.categoryOffcialListData = categoryOffcialListData;
                 this.isCategoryDataLoaded = true;


              },
              error => {

              }
              );

          },
          error => {

          }
          );


        }


      }
      

    }
    else {

      this.staffTypeId = 27;

      var categoryAvailable = _.some( this.categoryPersonalData, item => {
        return item.lookupValueName === this.staffCategory;
      });

      if(categoryAvailable) {
        this.isError = true;
        this.errorMessage = 'Category already available';
        this.isStaffLoaded = true;
      }

      else {

          this.isStaffLoaded = false;

           if(!this.isEditStaffCategory) {

              let details = {
        
                "lookupTypeId": this.staffTypeId,
                "lookupValueName": this.staffCategory,
                "description": this.staffCategory,
                "isActive": true,
                "insertedBy": parseInt(this.cookieService.get('userId')),
                "insertedOn": "2019-11-16T14:50:08.217Z",
                "updatedBy": 0,
                "updatedOn": "2019-11-16T14:50:08.217Z"

              }

              let params = {
                lookupvalue: details
              }

              this.lookupService.addLookupValue(params).subscribe((res:any) => {


                  this.isStaffLoaded = true;
                  this.isCategoryDataLoaded = false;

                  let params = {
                    LookupTypeId: this.staffTypeId
                  }

                  this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

                     var categoryPersonalListData = res.filter(item => {
                       return item.isActive;
                     });

                     this.categoryPersonalListData = categoryPersonalListData;
                     this.isCategoryDataLoaded = true;


                  },
                  error => {

                  }
                  );

              },
              error => {

              }
              ); 


           }

           else {

             let details = {

              "lookupValueId": this.staffCategoryId,
              "lookupTypeId": this.staffTypeId,
              "lookupValueName": this.staffCategory,
              "description": this.staffCategory,
              "isActive": true,
              "insertedBy": 0,
              "insertedOn": "2019-11-16T14:50:08.217Z",
              "updatedBy": parseInt(this.cookieService.get('userId')),
              "updatedOn": "2019-11-16T14:50:08.217Z"

            }

            let params = {
              lookupvalue: details
            }

            this.lookupService.updateLookupValue(params).subscribe((res:any) => {


                  this.isStaffLoaded = true;
                  this.isCategoryDataLoaded = false;
                  this.isEditStaffCategory = false;

                  let params = {
                    LookupTypeId: this.staffTypeId
                  }

                  this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

                     var categoryPersonalListData = res.filter(item => {
                       return item.isActive;
                     });

                     this.categoryPersonalListData = categoryPersonalListData;
                     this.isCategoryDataLoaded = true;


                  },
                  error => {

                  }
                  );

            },
            error => {

            }
            ); 


           }

        }

    }



  }

  ngOnInit() {

    let params = {
      LookupTypeId: 25
    }

    //get all staff type
      this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

        this.staffCategoryData = res;

        this.isStaffLoaded = true;


      },
      error => {

      }
      );

    let categoryParams = {
      LookupTypeId: 26
    }

  	//get all offical category
    this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res:any) => {

      var categoryOffcialListData = res.filter(item => {
        return item.isActive;
      });


      this.categoryOffcialListData = categoryOffcialListData;

      let categoryPersonalParams = {
        LookupTypeId: 27
      }

      //get all personal category
      this.lookupService.getLookupValueByLookupTypeId(categoryPersonalParams).subscribe((res:any) => {

        var categoryPersonalListData = res.filter(item => {
          return item.isActive;
        });

        this.categoryPersonalListData = categoryPersonalListData;

        this.isCategoryDataLoaded = true;


      },
      error => {

      }
      );


    },
    error => {
      
    }
    );


    // delete lookupvalue
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        if(this.staffDeleteTypeId == "115"){

          var params = {
            lookupValueId: this.categoryOffcialListData[index].lookupValueId,
            updateUserId: parseInt(this.cookieService.get('userId'))
          }

        }
        else {

          var params = {
            lookupValueId: this.categoryPersonalListData[index].lookupValueId,
            updateUserId: parseInt(this.cookieService.get('userId'))
          }

        }
        

        this.isCategoryDataLoaded = false;

        this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {

          if(this.staffDeleteTypeId == "115"){
            this.categoryOffcialListData.splice(index, 1);
          }
          else {
            this.categoryPersonalListData.splice(index, 1);
          }
          this.isCategoryDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    } )

  }

}
