import { Component, OnInit, ViewChild, HostListener } from '@angular/core';

import { StaffService } from 'src/app/api/controllers/Staff';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import{Router} from '@angular/router'
import * as _ from 'underscore';
import * as moment from 'moment'
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ConstantsService } from 'src/app/shared/services/constants.service';
@Component({
  selector: 'app-staff-attendance',
  templateUrl: './staff-attendance.component.html',
  styleUrls: ['./staff-attendance.component.scss']
})
export class StaffAttendanceComponent implements OnInit {
  
  entryListData:any;

  isEntryDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  entry:any;
  entryData:string = "";

  staffDetails:any;
  gateDataList:any;

  officalStaffsDataList:any;
  personalStaffsDataList:any;

  selectedInput:string = "";
  columnField: any = {}

  columnData:any ;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  constructor(
    private staffService:StaffService,
    private lookupService: LookupService,
	private cookieService: CookieService,
	private constantsService:ConstantsService,
	private router:Router
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  getInGateName(id) {
  	
  	var data = this.gateDataList.filter( item => {
  		return item.gateId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.inGateId = "";
  		return ""
  	}
  	else {
  		this.entry.inGateId = data[0].gateName;
  		return data[0].gateName;
  	}

  }

  getOutGateName(id) {
  	
  	var data = this.gateDataList.filter( item => {
  		return item.gateId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.outGateId = "";
  		return ""
  	}
  	else {
  		this.entry.outGateId = data[0].gateName;
  		return data[0].gateName;
  	}

  }

  getStaffName(id) {

  	var data = this.staffDetails.filter( item => {
  		return item.staffId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.firstName = "";
  		return ""
  	}
  	else {
  		this.entry.firstName = data[0].firstName;
  		return data[0].firstName;
  	}
  	

  }


  getJobTitle(id){

  	var data = this.staffDetails.filter( item => {
  		return item.staffId == id;
  	});

  	if(data === undefined || data.length === 0) {
  		this.entry.jobTitle = "";
  		return ""
  	}
  	else {
  		var selectedStaffCateogryId = data[0].staffCategoryId;
  		var selectedSubStaffCateogryId = data[0].staffSubCategoryId;

  		// offical staff
  			if(selectedStaffCateogryId == 115) {
  				
  				let name = this.officalStaffsDataList.filter(item => {
  					if(item.lookupValueId == selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})


  				 if(name === undefined || name.length == 0){
  				 	this.entry.jobTitle = "";
  				 	return ""
  				 }
  				 else {
  				 	this.entry.jobTitle = name[0].lookupValueName;
  				 	return name[0].lookupValueName;
  				 }

  				

  			}
  			else {
  				let name = this.personalStaffsDataList.filter(item => {
  					if(item.lookupValueId == selectedSubStaffCateogryId){
  						return item.lookupValueName;
  					}
  				})
  				if(name === undefined || name.length == 0){
  				 	this.entry.jobTitle = "";
  				 	return ""
  				 }
  				 else {
  				 	this.entry.jobTitle = name[0].lookupValueName;
  				 	return name[0].lookupValueName;
  				 }
  			}



  	}

  }

  onGlSearchFilter() {

    if(this.entryData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.entryData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }
  
  @HostListener('window:onEditAttendenceStaff', ['$event.detail'])
  onEditAttendenceStaff(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let attendanceId = dataRecord.attendanceId
    this.router.navigateByUrl('/ams/staff/edit-entry/' + attendanceId);
  }

  ngOnInit() {

	var cellsrenderer = (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      }
  
      var columnrenderer = (value) => {
        return '<div style="padding: 14px">' + value + '</div>';
      }

      this.columnData = [{
        text: 'Name',
        datafield: 'apartmentId',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },{
        text: 'Job itle',
        datafield: 'attendanceId',
        width: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
          text: 'Intime',
          datafield: 'inTime',
          minwidth: 120,
          cellsrenderer: (row, column, value): string => {
            return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
          },
          renderer: columnrenderer
      },
      {
          text: 'Outtime',
          datafield: 'outTime',
          minwidth: 120,
          cellsrenderer: (row, column, value): string => {
            return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
          },
          renderer: columnrenderer
      },
      {
          text: 'Ingate',
          datafield: 'inGateId',
          minwidth: 120,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
          text: 'Outgate',
          datafield: 'outGateId',
          minwidth: 150,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
        text: 'Comments',
        datafield: 'insertedBy',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
      {
          text: 'Actions',
          cellsalign: 'center', 
          align: 'center',
          minwidth: 120,
          
          cellsrenderer: (row,coloumn,value): any => {
			return '<div class="simple-actions">'
					+	'<a href="javascript:void(0)" class="mr-2" onClick="editAttendenceStaff('+ row +')">'
					+	'<i class="icon fa fa-pencil" aria-hidden="true"></i>'
					+	'</a>'
					+'</div>'
          },
          renderer: columnrenderer
      }];
  	this.entry = {};


  	this.staffDetails = [];
  	this.gateDataList = [];

  	this.officalStaffsDataList = [];
  	this.personalStaffsDataList = [];

	let params = {
		apartmentId: parseInt(this.cookieService.get('apartmentId'))
	}

  	this.staffService.getAllAttendancesByApartmentId(params).subscribe((res:any) => {

    //filter active true items
    this.entryListData = res.filter(data => {
      return data.isActive
    });

    _.each(this.entryListData, item => {
    	
    	this.staffService.getStaffByStaffId(item.staffId).subscribe((staff:any) => {

    		this.staffDetails.push(staff[0]);

    	});

	});
	console.log('entryListData',this.entryListData)
	this.gridSourceData = {
		localdata: this.entryListData,
		datatype: "array"
	  }
	  this.entryListData = new jqx.dataAdapter(this.gridSourceData);
	let dataListparams = {
		apartmentId: parseInt(this.cookieService.get('apartmentId'))
	}

    //get all gates
	this.staffService.getAllGatesByApartmentId(dataListparams).subscribe((res:any) => {
		this.gateDataList = res;
	});

	let officialListparams = {
		LookupTypeId: 26
	}

	//get all offical staff 
    this.lookupService.getLookupValueByLookupTypeId(officialListparams).subscribe((res:any) => {
       
 		this.officalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});

    },
    error => {
      
    }
	);
	
	let personalListparams = {
		LookupTypeId: 27
	}


    //get all personal staffs 
    this.lookupService.getLookupValueByLookupTypeId(personalListparams).subscribe((res:any) => {
       
 		this.personalStaffsDataList = res.filter(item => {
 			return item.isActive;
 		});


    },
    error => {
      
    }
    );


    this.totalItems = this.entryListData.length;

    if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }

    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isEntryDataLoaded = true;

  });
  }

}


function editAttendenceStaff(row){
	var event = new CustomEvent('onEditAttendenceStaff', {
	  detail: {
		  rowId: row
	  }
	})
	window.dispatchEvent(event);
  }
  (window as any).editAttendenceStaff = editAttendenceStaff;