import { Component, OnInit, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { StaffService } from 'src/app/api/controllers/Staff';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';
import * as _ from 'underscore';

@Component({
  selector: 'app-staff-shifts',
  templateUrl: './staff-shifts.component.html',
  styleUrls: ['./staff-shifts.component.scss']
})
export class StaffShiftsComponent implements OnInit {
  
  isShiftsLoaded:boolean = true;

  isShiftTableLoaded:boolean = false;

  shiftTypeList:any;

  shift:any;
  shiftId:any;
  shiftTypeId:string = "";
  shiftStartTime:any = "";
  shiftTotalHrs:number = 0;

  shiftsListData:any;
  shiftData:string = "";

  shiftType:string = "";

  isEditShift:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  isAddShiftLoaded:boolean = false;

  modalService:any;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private staffService:StaffService,
  	private lookupService:LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getShiftType(id) {
    
    var data = _.filter( this.shiftTypeList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.shiftType = data[0].lookupValueName;
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  submitShiftsForm(form) {

    this.isShiftsLoaded = false;

    if(!this.isEditShift) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "shiftTypeId": parseInt(this.shiftTypeId),
        "startTime": moment(this.shiftStartTime).format('HH:mm'),
        "totalHours": this.shiftTotalHrs,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": "2019-12-03T06:11:51.972Z"

      }

      let params = {
        shift: details
      }

      this.staffService.addShift(params).subscribe((res:any) => {

        if(res.message) {

          this.isShiftsLoaded = true;
          this.sharedService.setAlertMessage("Shift added Successfully!");

          this.getAllShifts();
        }

      },
      error => {
        
      }
      );

   }
   else {

     let details = {
        "shiftId": this.shiftId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "shiftTypeId": parseInt(this.shiftTypeId),
        "startTime": moment(this.shiftStartTime).format('HH:mm'),
        "totalHours": this.shiftTotalHrs,
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        shift: details
      }

      this.staffService.updateShift(params).subscribe((res:any) => {

        if(res.message) {
          this.isShiftsLoaded = true;
          this.sharedService.setAlertMessage("Shift updated Successfully!");

          this.getAllShifts();
        }

      },
      error => {
        
      }
      );

   }
    

  }

  addShift(){

    this.isEditShift = false;
    this.isAddShiftLoaded = false;

    setTimeout(() => {

      this.isAddShiftLoaded = true;

    }, 10);

    setTimeout(() => {

      var elem = document.querySelector('.add-shift-card');
      var scrollTo = elem.getBoundingClientRect().top;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 10);

  }

  editShift(shift){

    this.isAddShiftLoaded = false;

    setTimeout(() => {
      this.isAddShiftLoaded = true;
    }, 10);

    this.isEditShift = true;
    this.shiftTypeId = shift.shiftTypeId;
    this.shiftStartTime = moment(shift.startTime, 'HH:mm').toDate();
    this.shiftTotalHrs = shift.totalHours;
    this.shiftId = shift.shiftId;

    setTimeout(() => {

      var elem = document.querySelector('.add-shift-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);

  }

  closeShift(){
    this.isAddShiftLoaded = false;
  }

  showConfirmModal(item) {
    this.modalService.showConfirmModal(item);
  }

  getAllShifts(){

    this.isShiftTableLoaded = false;

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.staffService.getAllShiftsByApartmentId(params).subscribe((res:any) => {

      this.shiftsListData = res.filter(item => {
        return item.isActive;
      });


      this.totalItems = this.shiftsListData.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }

      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isShiftTableLoaded = true;

    },
    error => {
      
    }
    );

  }

  ngOnInit() {

  	this.shift = {};

    this.getAllShifts();

    let params = {
      LookupTypeId: 28
    }

  	//shift type
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
          	

       this.shiftTypeList = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {

      if(item != null){

        var params = {
          shiftId: item.shiftId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isShiftTableLoaded = false;

        this.staffService.deleteShift(params).subscribe((res:any) => {

          this.shiftsListData = this.shiftsListData.filter((type) => type.shiftId !== item.shiftId);
          this.sharedService.setUnitListDeleteIndex(null);
          this.isShiftTableLoaded = true;


        }, error => {
          console.log(error);
        });

        
      }

    } )

  }

}
